    USER: state => state.user,
    error: state => state.error,
    authState: state => state.status,
    isLoggedIn: state => !!Object.keys(state.user).length,

    //   
    CURRENT_SCREEN: state => state.current_screen,
    IS_OPENED: state => state.isOpened, //      
    IS_RESULT: state => state.isResult, //  
    IS_OPENING: state => state.isOpening, //  
    IS_BOOSTER_INFO: state => state.isBoosterInfo, //     
    IS_CLICK_ON_BOOSTER: state => state.isClickOnBooster, //      

    //  
    BOOSTERS: state => state.boosters, //      
    COLL_DATA: state => state.coll_data, //   
    BOOSTER_DATA: state => state.booster_data, //  
    OPENING_CARDS_COUNT: state => state.openingCardsCount, //       

    // 
    NOTIF_isNotLoggedIn: state => state.notif_isNotLoggedIn, //   - 
    NOTIF_isNotEnoughMoney: state => state.notif_isNotEnoughMoney, //   
